.charts-css {
  // Reset
  margin: 0;
  padding: 0;
  border: 0;

  // Position
  position: relative;
}

.charts-css,
.charts-css * {
  box-sizing: border-box;
}

ul.charts-css,
ol.charts-css {
  list-style-type: none;

  // Counter
  counter-reset: data;

  li {
    counter-increment: data;
  }
}

table.charts-css {
  // Counter
  tbody {
    counter-reset: data;

    tr {
      counter-increment: data;
      counter-reset: dataset;

      td {
        counter-increment: dataset;
      }
    }
  }
}
